        .menu-item {
            min-width: 30%;
            height: 240px;
            flex: 1 1 auto;
            display: flex;
            align-items: center;
            justify-content: center;
            border: 1px solid black;
            margin: 0 7.5px 15px;
            margin-right: 7.5px;
            overflow: hidden;
            &:hover {
                cursor: pointer;
                &.background-image {
                    transform: scale(1.5);
                    transition: transform 0.5s cubic-bezier(0.25, 0.45, 0.45, 0.95);
                }
                & .content {
                    opacity: 0.9;
                }
            }
            &.large {
                height: 380px;
            }
            &:last-child {
                margin-left: 7.5px;
            }
            .background-image {
                width: 100%;
                height: 100%;
                background-position: center;
                background-size: cover;
            }
            .content {
                height: 90px;
                padding: 0 25px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                border: 1px solid black;
                background-color: rgba(255, 255, 255, 0.652);
                font-weight: bold;
                margin-bottom: 6px;
                font-size: 22px;
                color: #4a4a4a;
                opacity: 0.7;
                position: absolute;
            }
            .title {
                font-size: 20px;
                font-weight: 600;
            }
            .subtitle {
                font-weight: 100;
                font-size: 16px;
            }
        }